import "../css/components/filterbar.css";
import { useState } from "react";

interface FilterBarProps {
  onSearch: (searchTerm: string, date?: string) => void;
  onReset: () => void; // Neue Callback-Funktion für Reset
}

export default function FilterBar({ onSearch, onReset }: FilterBarProps) {
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedDate, setSelectedDate] = useState("");

  const handleSearch = () => {
    onSearch(searchTerm, selectedDate);
  };

  const handleReset = () => {
    setSearchTerm("");
    setSelectedDate("");
    onReset(); // Callback für das Zurücksetzen der Suche
  };

  return (
    <div className="filter-bar">
      <div className="input-group">
        <input
          type="text"
          placeholder="Suche nach Workshops..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="search-input"
        />
        <input
          type="date"
          value={selectedDate}
          onChange={(e) => setSelectedDate(e.target.value)}
          className="filter-date"
        />
      </div>
      <div className="button-group">
        <button className="search-button" onClick={handleSearch}>
          Suche
        </button>
        <button className="reset-button" onClick={handleReset}>
          Zurücksetzen
        </button>
      </div>
    </div>
  );
}
